
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import axios from 'axios'
import 'mavon-editor/dist/css/index.css'
export default {
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data:function(){
return{
title:'',
content:'',
toastText:'',
isShowToast:false,
toolbars:{
bold: true, // 粗体
italic: true, // 斜体

underline: true, // 下划线








imagelink: true, // 图片链接






/* 1.3.5 */




/* 1.4.2 */

/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */

preview: true 
}}
},
methods:{

uploadImage:function(pos,file){
var app = this;

console.log(pos)  
console.log(file) 
var params = new FormData();
params.append('smfile', file);
axios.post('https://sm.ms/api/v2/upload',params,{
headers: { 'Content-Type': 'multipart/form-data' },
}).then(function(res){


app.$refs.md.$img2Url(pos,res.data.message.url)
})
},
addtopic:function(){
var app = this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'  
}).then(function(res){
console.log(res);
app.$router.replace('/topiclist/dev')
}).catch(function(error){
app.isShowToast=true;
app.toastText='错误';
})
}
}
}



