import Vue from 'vue'
import Router from 'vue-router'
import TopicList from '../components/TopicList.vue'
import Topic from '../components/Topic.vue'
import Login from '../components/Login.vue'
import User from '../components/User.vue'
import Center from '../components/Center.vue'
import Logout from '../components/Logout.vue'
import Create from '../components/Create.vue'
import Edit from '../components/Edit.vue'
import Msg from '../components/Msg.vue'
import User_Collect from '../components/User_Collect.vue'

Vue.use(Router);
//解决路由地址重复点击报错的问题
const originalPush = Router.prototype.push;
Router.prototype.push = function push(localtion) {
    return originalPush.call(this, localtion).catch(err => err)
}

export default new Router({
    routes: [{
            path: '/',
            redirect: '/topiclist/all'
        },
        {
            path: '/topiclist/:name',
            component: TopicList,
            props: true //把动态路由参数作为props属性来传递
        },
        {
            path: '/topic/:id',
            component: Topic,
            props: true
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/logout',
            component: Logout
        },
        {
            path: '/user/:loginname',
            component: User,
            props: true
        },
        {
            path: '/collections/:loginname',
            component: User_Collect,
            props: true
        },
        {
            path: '/center',
            component: Center
        },
        {
            path: '/create',
            component: Create
        },
        {
            path: '/topic/:id/edit',
            component: Edit,
            props: true
        },
        {
            path: '/msg',
            component: Msg
        }
    ]
})


