
import {mapState} from 'vuex'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name:'topiclist',
props:['name'],
components:{
Cell,Group,Scroller, Tab, TabItem
},
data:function(){
return{
lists:[],
page:1,//当前页数第一页
limit:8,//每页显示的数量

pullupconfig:{
content:'上拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
},
pulldownconfig:{
content:'下拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
}
}
},
computed:{
...mapState(['topics'])
},
methods:{
routeTo(index){

this.$router.push('/topiclist/'+this.topics[index].name)
},
loadMoreDate(){

this.page=this.page+1;
var app = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){









app.lists.push(...res.data.data)



app.$refs.pull.donePullup();

})
},
downMore(){

this.page=1;
var app = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){










app.lists = res.data.data




app.$refs.pull.donePulldown();
})
}
},



beforeRouteEnter(to,from,next){




next(function(vm){

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){
console.log(res.data);
vm.lists=res.data.data
})
})
},
beforeRouteUpdate(to,from,next){
var that = this
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
that.lists = res.data.data
})
next();
}

}



