
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components:{
ButtonTab, ButtonTabItem,Swipeout, SwipeoutItem, SwipeoutButton,Group,Cell ,XButton
},
data:function(){
return{
tabIndex:0,
hasReadMsgs:null,
hasNotReadMsgs:null
}
},
created:function(){
this.getAllMsgs();
},

methods:{
getAllMsgs:function(){
var app = this;
this.$http.get('/messages',{
params:{

accesstoken:window.localStorage.getItem('accesstoken')
}
}).then(function(res){
console.log(res.data)
if(res.data.success){
app.hasReadMsgs=res.data.data.has_read_messages
app.hasNotReadMsgs=res.data.data.hasnot_read_messages
}
})
},

markRead:function(mid){
this.$http.post('/message/mark_one/'+mid,{

accesstoken:window.localStorage.getItem('accesstoken')

}).then(function(res){
console.log(res.data);
app.getAllMsgs();
})
},

markAll:function(){
var app = this;
this.$http.post('/message/mark_all/',{

accesstoken:window.localStorage.getItem('accesstoken')

}).then(function(res){


app.$router.go(0);
})
}
}

}



